const courier = document.querySelector('#courier');
const cars = document.querySelector('.corsik_yaDeliveryMap__carsBlock');
const manipulator = document.querySelector('.corsik_yaDeliveryMap__manipulators');
const manipulatorRadioBtn = document.querySelector('#manipulators');
const carRadioBtn = document.querySelector('#cars');

courier.addEventListener('change', () => {
    manipulator.style.display = 'none';
    cars.style.display = 'none';
});

manipulatorRadioBtn.addEventListener('change', () => {
    manipulator.style.display = 'flex';
    cars.style.display = 'none';
});

carRadioBtn.addEventListener('change', () => {
    manipulator.style.display = 'none';
    cars.style.display = 'flex';
});



